
export default {
  onLaunch: function () {
    require('./utils/sdk-qq.3.6.0.js')
    let clientID = '7d7011b1c491d19f58be'  // 应用名称: 北京一路校跑的第一个小程序
    qq.BaaS.init(clientID)
    qq.showLoading('接入中...')
    this.$store.dispatch('user/getUserInfo')
      .then(res => {
        qq.hideLoading()
      })
      .catch(err => {
        qq.hideLoading()

      })
  },
  onShow: function () {
    console.log('App Show')
  },
  onHide: function () {
    console.log('App Hide')
  }
}
